.detail-col {
  height: 80px;
}

.checkoutProdListContainer {
  height: 100%;
  min-height: 100%;
  background-color: #f0eeee;
}

.rate-section {
  margin: 16px;
}

.rate-title {
  text-align: start;
}

.rate-form {
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 16px;
}

.first-line {
  display: flex;
  flex-direction: row;
  margin: 8px;
}

.sec-line {
  display: flex;
  flex-direction: column;
  margin: 8px;
}

.rate-area {
  align-self: flex-start;
}

.rate-button {
  align-self: flex-start;
  background: white;
  margin-top: 16px;
}


html body #app #app>div{
  height: 100%;
  background-color: #f8f9fa;
  background-color: #eedf0f;

}

